$animationDuration: 500ms;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body,
#root,
.app {
  width: 100vw;
  height: 100vh;
  background: #f8f8f8;
  font-family: 'Segoe UI', 'Roboto', 'Courier', 'Helvetica', Tahoma, Geneva, Verdana, sans-serif;
}

.react-transform-component,
.react-transform-element {
  width: 100vw !important;
  height: 100vh !important;
}

.image-wrapper,
.image,
.konvajs-content,
canvas {
  position: static !important;
  width: auto !important;
  height: auto !important;
  max-width: 90vw !important;
  max-height: 90vh !important;
}

.react-transform-component,
.react-transform-element,
.image-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.image-wrapper {
  position: relative !important;
  background: white;
  box-shadow: 0 5px 15px -3px #00000020;
  width: 3308px;
  height: 2339px;
}

.bounding_box {
  position: absolute;
  display: block;
  background: #007c92aa;
  animation: fadeIn $animationDuration forwards;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  color: white;
  text-decoration: none;
  border-radius: 2px;

  &.highlight {
    animation: fadeIn $animationDuration forwards, highlight 500ms normal;
  }
}

.image {
  &.pending {
    opacity: 0;
  }

  &.loaded {
    animation: fadeIn $animationDuration forwards;
  }
}

.help {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes highlight {
  from {
    box-shadow: 0 0 0 2px #007c92;
  }

  to {
    box-shadow: 0 0 0 2px transparent;
  }
}
